$dg: #1a3c00;
$gg: #f6c92b;
$gt: rgba(246,201,43,0.4);
$dtr: rgba(26,60,0,0.4);
$gtd: rgba(246,201,43,0.9);
@font-face {
	font-family: norse;
	src: url(NB.ttf); }
* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box; }
body {
	overflow-x: hidden;
	margin: 0;
	padding: 0;
	background-image: url(vec.png);
	-webkit-background-size: cover;
	background-size: cover;
	width: 100%;
	height: 100%; }
.container {
	width: 80%;
	margin: 0 auto; }
header {
	width: 80%;
	clear: both;
	margin: 0 auto;
	margin-top: 25px;
	font-size: 0;
	background: $gt;
	border-radius: 150px;
	img {
		display: inline-block;
		height: 218px;
		width: 20%;
		border-radius: 150px; } }
.logo {
	display: inline-block;
	height: 200px;
	vertical-align: top;
	width: 60%;
	font-size: 0;
	h1 {
		line-height: 200px;
		width: 100%;
		vertical-align: top;
		position: relative;
		margin: 0;
		font-size: 150px;
		text-align: center;
		font-family: norse;
		color: $dg;
		text-shadow: rgb(245, 200, 40) -9px 9px 1px; } }
#amb {
	display: none;
	height: 50px;
	position: absolute;
	vertical-align: top;
	left: 0;
	text-align: left;
	padding-left: 11%;
	font-weight: 500;
	text-decoration: none;
	font-size: 15px;
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	text-transform: uppercase;
	color: $dg;
	background: none;
	border: 0;
	width: 100%;
	&:first-letter {
		font-size: 20px;
		font-weight: 700; } }
.menubar {
	margin-top: 25px; }
.menu {
	background: #fff;
	font-size: 0;
	ul {
		padding: 0;
		width: 100%;
		font-size: 0;
		display: inline-block;
		li {
			font-size: 0;
			display: inline-block;
			width: 20%;
			text-align: center;
			a {
				height: 45px;
				padding: 9px;
				display: inline-block;
				width: 100%;
				font-weight: 500;
				text-decoration: none;
				font-size: 15px;
				font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
				text-transform: uppercase;
				color: $dg;
				&:first-letter {
					font-size: 20px;
					font-weight: 700; }
				&:hover {
					background: $gtd; } } } } }
.media {
	display: inline-block;
	width: 20%;
	font-family: norse;
	vertical-align: top;
	ul {
		padding: 0;
		padding-top: 50px;
		margin: 0;
		width: 100%;
		li {
			display: block;
			height: 50%;
			width: 100%;
			text-align: center;
			padding-top: 10px;
			&:hover {
				background: $gt;
				border-radius: 50px; }
			a {
				display: inline-block;
				font-size: 30px;
				text-decoration: none;
				color: $dg;
				width: 100%; } } } }
.main {
	font-size: 0; }
.main-wrapp {
	width: 70%;
	font-size: 0;
	display: inline-block;
	text-align: center;
	background: $gt;
	h3 {
		margin: 0;
		padding: 10px;
		font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
		font-size: 18px;
		background: $dg;
		color: $gg;
		font-weight: 700;
		height: 40px; }
	img {
		width: 100%;
		display: inline-block;
		border-top: 0; } }
.text-main {
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	font-size: 15px;
	padding: 10px;
	margin: 0;
	display: inline-block;
	vertical-align: top;
	h4 {
		margin: 0;
		text-align: center;
		text-transform: uppercase; }
	p {
		padding: 0 1%;
		text-indent: 20px;
		margin: 0;
		text-align: justify; } }

aside {
	display: inline-block;
	width: 30%;
	vertical-align: top;
	background: #fff;
	border-top: 40px solid $dg;
	margin: 0;
	height: 672px;
	p {
		font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
		font-size: 16px;
		text-align: center;
		margin: 0;
		height: 35px;
		margin-top: -35px;
		color: $gg;
		line-height: 35px; }
	ul {
		height: 100%;
		padding: 0;
		li {
			height: 20%;
			a {
				text-decoration: none;
				color: #000;
				font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
				font-size: 20px;
				text-align: center;
				display: block;
				margin: 0;
				height: 100%;
				width: 100%;
				border-bottom: 2px solid $dg;
				padding: 0 15px;
				&:hover {
					background: $gg; } } } } }




@media screen and (max-width: 1280px) {
	.logo {
		width: 50%;
		padding: 0;
		h1 {
			font-size: 120px; } }
	.media {
		width: 30%;
		a {
			font-size: 25px; } } }


@media screen and (max-width: 1024px) {
	.logo {
		width: 80%;
		h1 {
			font-size: 110px; } }
	.media {
		display: none; } }

@media screen and (max-width: 768px) {
	aside {
		display: none; }

	.main-wrapp {
		margin-top: 50px;
		clear: both;
		width: 100%; }
	#amb {
		display: block;
		background: $gg; }
	.menu {
		float: left;
		z-index: 100;
		position: relative;
		left: 0;
		top: 50px;
		width: 42%;
		height: 0;
		ul {
			display: none;
			li {
				width: 100%;
				background: $gtd;
				text-align: left; } } }
	header {
		img {
			width: 25%;
			margin: 0; }
		.logo {
			width: 75%;
			h1 {
				font-size: 100px; }
			h2 {
				display: none; } } } }

@media screen and (max-width: 640px) {
	header {
		img {
			width: 30%; }
		.logo {
			width: 70%;
			h1 {
				padding-top: 20px;
				line-height: 0.8;
				font-size: 95px; } } } }

@media screen and (max-width: 320px) {
	header {
		img {
			display: none; }
		.logo {
			width: 100%; } } }
